@font-face {
	font-family: 'Proxima';
	src: url(../src/fonts/ProximaNova-Regular.otf);
}
@font-face {
	font-family: 'Proxima Bold';
	src: url(../src/fonts/ProximaNova-Bold.otf);
}
@font-face {
	font-family: 'Flamenco-Regular';
	src: url(../src/fonts/Flamenco-Regular.ttf);
}
@font-face {
	font-family: 'Flamenco-Light';
	src: url(../src/fonts/Flamenco-Light.ttf);
}
@font-face {
	font-family: 'FjallaOne Regular';
	src: url(../src/fonts/FjallaOne-Regular.ttf);
}

body {
	/* background: #f8f8f8; */
	color: #606060;
	font-family: 'Flamenco-Regular','Proxima', sans-serif;
	font-size: 16px;
	font-weight: 400;
	position: relative;
}
html, body {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	overflow-x: hidden;
	text-align: center;
	vertical-align: middle;

	scroll-behavior: smooth;

}

h1, h2, h3, h4, h5, h6 {

	font-family: "FjallaOne Regular";
	text-transform: uppercase;
}
p {
	letter-spacing: 1px;
	line-height: 28px;
	font-family: "Flamenco-Regular";
	text-transform: uppercase;
}
hr {
	border-color: #e0e0e0;
	margin: 0 auto 20px auto;
	width: 100px;
}
/* h1 {
	font-size: 60px;
} */
h2 {
	font-size: 48px;
}
h3 {
	font-size: 30px;
}
h4 {
	font-size: 24px;
}
h5 {
	font-size: 18px;
}
h6 {
	font-size: 16px;
}
.title h2 {
	color: #202020;
	line-height: 20px;
	padding-bottom: 30px;
}
.title p {
	padding-top: 28px;
	padding-bottom: 30px;
}
.parallax-section {
	background-attachment: fixed !important;
	background-size: cover !important;
}
.fjalaone {
	font-family: 'FjallaOne Regular';
}

.flamReg {
	font-family: 'Flamenco-Regular';
}

.flamLig {
	font-family: 'Flamenco-Light';
}
a {
	&:hover {
		text-decoration: none;
	}	
}

section {

	padding-top: 6.5rem;
    padding-bottom: 7rem;

	h3 {
		color: #202020;
		font-family: "FjallaOne Regular";
		font-size: 36px;
		text-transform: uppercase;
	}
}


//TOASTER 
.Toastify__progress-bar--default {
	background: #F6A64F;
	
}
