//prevent Modal effect in Body and Navbar
body.modal-open {
    padding-right: 0px !important;
}
nav.navbar {
    padding: .5rem 1rem !important;
}

.modal.fade{
    
    .modal-content {
        border: none;
        border-radius: 0;

    }

    button.close {
        position: absolute;
        top: -0.375rem;
        right: -0.375rem;
        width: 2.75rem;
        height: 2.75rem;
        z-index: 100;
    }
}